import {
    getComments
} from '@/api/article'
export default {
    name: 'ArticleComments',
    props: {
        article: {
            type: Object,
            required: true
        }

    },
    data() {
        return {
            comments: []
        }
    },
    async mounted() {
        const {
            data
        } = await getComments(this.article.slug)
        this.comments = data.comments
    },
    components: {},
    computed: {},
    methods: {},
}
