import articleMeta from './components/article-meta'
import ArticleComments from './components/article-comment'
import {
    getArticle
} from '@/api/article'

import MarkdownIt from 'markdown-it'
export default {
    name: 'ArticleIndex',
    async asyncData({
        params
    }) {
        const {
            data
        } = await getArticle(params.slug)
        const {
            article
        } = data
        const md = new MarkdownIt()
        article.body = md.render(article.body)
        console.log(data);
        return {
            article
        }
    },

    components: {
        articleMeta,
        ArticleComments
    },
    head() {
        return {
            // hid 唯一标识编号
            title: `${this.article.title} - RealWorld`,
            meta: [{
                hid: 'des',
                name: 'des',
                content: this.article.description
            }]
        }
    },
    data() {
        return {}
    },
    computed: {},
    methods: {},
}
